@import 'config/mixins';
@import 'config/variables';

@include font-face('Carisma', '../assets/fonts/CarismaClassic-300Light', 300);
@include font-face('Carisma', '../assets/fonts/CarismaClassic-500Medium', 500);
@include font-face('Carisma', '../assets/fonts/CarismaClassic-400Regular', 500);

@include font-face('Helvetica', '../assets/fonts/HelveticaNeueCyr-Roman', 400);
@include font-face('Helvetica', '../assets/fonts/HelveticaNeueCyr-Medium', 500);
@include font-face('Helvetica', '../assets/fonts/helveticaneuecyr-bold', 600);

.time-to-buy {
  text-align: center;
  font-family: 'Carisma', serif;

  &--rules {
    margin: 0 auto;
    margin-bottom: 20px;
    text-align: start;
    max-width: 774px;

    @include media(lg) {
      margin-bottom: 77px;
    }
  }

  &__gif {
    margin-bottom: 30px;
    text-align: center;

    @include media(lg) {
      margin-bottom: 61px;
    }

    &--2 {
      margin-bottom: 47px;

      @include media(lg) {
        margin-bottom: 68px;
      }
    }

    img {
      max-width: 1440px;
    }
  }

  &__section {
    margin-bottom: 34px;
    position: relative;
    padding-bottom: 38px;

    @include media(lg) {
      margin-bottom: 34px;
      padding-bottom: 33px;
    }

    &--red {
      padding-bottom: 51px;

      @include media(lg) {
        padding-bottom: 71px;
      }

      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 79px;
        height: 7px;
        background-color: #ed2f48;
        transform: translateX(-43%);

        @include media(lg) {
          display: none;
        }
      }
    }

    &--solo {
      margin-bottom: 24px;

      @include media(lg) {
        margin-bottom: 25px;
      }
    }

    &--shops {
      margin-bottom: 86px;

      @include media(lg) {
        margin-bottom: 95px;
      }
    }

    &--brands {
      @include media(lg) {
        padding-bottom: 61px;
      }
    }
  }

  &__row {
    max-width: 700px;
    margin: 0 auto;
    margin-bottom: 31px;

    @include media(lg) {
      margin-bottom: 0;
      padding-left: 27px;
    }

    &--2 {
      max-width: 580px;
    }
  }

  &__title {
    margin: 0 auto;
    position: relative;
    font-size: 19px;
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 69px;
    text-align: center;
    max-width: 605px;
    width: 80vw;
    line-height: 1.6;

    @include media(lg) {
      margin-bottom: 54px;
    }

    &:after,
    &:before {
      content: "";
      position: absolute;
      top: 25%;
      width: 100vw;
      height: 3px;
      background-color: #ed2f48;

      @include media(lg) {
        top: 50%;
      }
    }

    &:after {
      right: 100%;
    }

    &:before {
      left: 100%;
    }

    &--2 {
      margin-bottom: 34px;
      max-width: 260px;
      
      @include media(lg) {
        margin-bottom: 33px;
      }

      &:after,
      &:before {
        top: 45%;
      }
    }

    &--rules {
      margin-bottom: 19px;
      font-size: 17px;
      font-weight: 600;
      font-family: 'Helvetica', sans-serif;
      text-transform: none;
      max-width: unset;
      width: unset;
      text-align: left;
      padding-left: 20px;

      &:before,
      &:after {
        display: none;
      }

      @include media(lg) {
        font-size: 19px;
        margin-bottom: 17px;
      }
    }
  }

  &__text {
    text-transform: uppercase;
    color: rgb(82, 82, 82);
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 0.5px;
    font-weight: 500;

    @include media(480px) {
      font-size: 24px;
    }

    @include media(lg) {
      font-size: 33px;
      line-height: 1.69;
      letter-spacing: 1px;
    }

    &:not(:last-child) {
      margin-bottom: 0px;
    }

    &--red {
      color: #ed2f48;
    }

    &--big {
      font-size: 38px;
      line-height: 1.25;
      font-weight: 600;

      @include media(480px) {
        font-size: 46px;
      }

      @include media(lg) {
        font-size: 54px;
        line-height: 1.16;
      }

      &:not(:last-child) {
        margin-bottom: 5px;

        @include media(lg) {
          margin-bottom: 3px;
        }
      }
    }

    &--percent {
      margin-top: -4px;
      font-size: 48px;
      line-height: 1;
      font-weight: 700;
      color: #ed2f48;

      @include media(lg) {
        margin-top: 0;
        line-height: 0.9;
      }

      &:not(:last-child) {
        margin-bottom: 5px;

        @include media(lg) {
          margin-bottom: 15px;
        }
      }
    }

    &--sm {
      font-size: 16px;
      font-weight: 500;

      @include media(480px) {
        font-size: 19px;
      }

      &:not(:last-child) {
        margin-bottom: 8px;

        @include media(lg) {
          margin-bottom: 14px;
        }
      }
    }

    &--lh {
      line-height: 2;

      &:not(:last-child) {
        margin-bottom: 6px;

        @include media(lg) {
          margin-bottom: 9px;
        }
      }
    }

    &--bold {
      font-weight: 600;
      color: #000;
    }

    &--span {
      line-height: 1.15;

      @include media(lg) {
        font-size: 48px;
        line-height: 1.1;
      }
    }

    &--solo {
      font-size: 16px;
      line-height: 1.5;
      font-weight: 300;
    }
  }

  &__brand {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 152px;
    margin-bottom: 56px;

    @include media(lg) {
      margin-bottom: 44.5px;
    }

    img {
      max-width: rem(130);

      @include media(480px) {
        max-width: rem(140);
      }

      @include media(sm) {
        max-width: unset;
      }
    }

    &--shop {
      margin-bottom: 53px;

      @include media(lg) {
        margin-bottom: 94px;
      }
    }
  }

  &__btn {
    margin: 0 auto;
    max-width: 393px;
    padding: 29px 38px;
    font-size: 13px;
    line-height: 1.5;
    letter-spacing: 0.5px;
    background-color: #d42027;
    color: #fff;
    font-weight: 500;
    transition: background-color .2s ease;

    &:hover {
      background-color: #CF0A2C;
    }

    @include media(475px) {
      font-size: 18px;
    }

    @include media(lg) {
      font-size: 16px;
      padding: 16px 54px;
    }
  }

  &__ul {
    padding: 0 10px 0 41px;
    list-style: disc;
  }

  &__li {
    margin-bottom: 18px;
    padding-left: 4px;
    font-size: 16px;
    line-height: 19px;
    font-weight: 500;
    font-family: 'Helvetica', sans-serif;

    @include media(lg) {
      margin-bottom: 20px;
      line-height: 1.1;
    }
  }
}